import request from "utils/request";
import PropTypes from 'prop-types';
import React from 'react';
import {ProgressBar} from 'react-bootstrap';
import axios from 'axios'

export default class AdminDownload extends React.Component {
    static propTypes = {
        license: PropTypes.object.isRequired,
        config: PropTypes.object,
    }

    constructor(props) {
        super(props);

        this.state = {
            macInput:"Mac",
            inputTxt:"3333",
            winInput:"win",
            filesWin:null,
            filesMac:null,
            percentmac:0,
            percentwin:0,
            showTxtmac:'',
            showTxtwin:''
        };
        this.fileInput = React.createRef();
    }
    onChangeInput=(e)=>{
        this.setState({inputTxt:this.fileInput.current.value});
    }

    onChangeMac=(e)=>{
      this.setState({macInput:e.currentTarget.files[0].name,filesMac:e.currentTarget.files[0]});
    }

    onChangeWin=(e)=>{
      this.setState({winInput:e.currentTarget.files[0].name,filesWin:e.currentTarget.files[0]});
    }

    onUpfileMac=(e)=>{
        //file, type, version
        if(!this.state.filesMac){
            alert("");
            return;
        }
        this.upFile({file:this.state.filesMac,type:'mac',version:this.state.inputTxt,loadProgress:(e)=>{
            let percent = e.loaded / e.total * 100 | 0;
            this.setState({percentmac:percent,showTxtmac:percent+"%"});
        }}).then((res)=>{
            if(res.data && res.data.file_infos && res.data.file_infos.length>0) this.setState({percentmac:100,showTxtmac:"!"});
        });
    }

    onUpfileWin=(e)=>{
        //file, type, version
        if(!this.state.filesWin){
            alert("");
            return;
        }
        this.upFile({file:this.state.filesWin,type:'win',version:this.state.inputTxt,loadProgress:(e)=>{
            let percent = e.loaded / e.total * 100 | 0;
            this.setState({percentwin:percent,showTxtwin:percent+"%"});
        }}).then((res)=>{
            if(res.data && res.data.file_infos && res.data.file_infos.length>0) this.setState({percentwin:100,showTxtwin:"!"});
        });
    }


    upFile ({ file, type, version,loadProgress }) {
        const formData = new window.FormData()
        formData.append('files', file);
        return request({
            url: `/api/v4/files/ao?file_type=${type}&version=${version}`,
            method: "POST",
            data:formData,
            onUploadProgress:function(progressEvent){ //
            if(progressEvent.lengthComputable){
                //lengthComputable
                //lengthComputablefalseprogressEvent.totalprogressEvent.loaded
                loadProgress(progressEvent);
            }
        },
        });
    };

    render() {
        return (
          <div className="main-wrapper">
            <div className="header">
                <div className="logo-box">
                <div className="logo"></div>
                <span className="desc-version">version 0.1.0</span>
                </div>
                <div className="subtitle">Make your IT team collaboration, <span className="green">O</span>nclound,<span className="green">N</span>ogap,<span className="green">E</span>fficient</div>
            </div>
            <div className="content">
                <ol className="form">
                <li className="form-item">
                    <label>Version:</label>
                    <div className="input-box">
                        <input id="version" type="text" ref={this.fileInput} onChange={this.onChangeInput}/>
                    </div>
                </li>
                <li className="form-item">
                    <label>Mac:</label>
                    <div className="input-box file-fix">
                    <input placeholder={this.state.macInput} disabled type="text" />
                    <input id="mac-file-input" className="file-input" type="file" onChange={this.onChangeMac}/>
                    </div>
                    <div className="suffix-btn" type="windows" onClick={this.onUpfileMac}></div>
                    {this.state.percentmac &&
                    (
                        <div>
                            <ProgressBar
                                className='post-image__progressBar'
                                now={this.state.percentmac}
                                active={this.state.percentmac === 100}
                            />
                            <div className='form-item-progressBar'>{this.state.showTxtmac}</div>
                        </div>
                    )}
                </li>
                <li className="form-item">
                    <label>Windows:</label>
                    <div className="input-box file-fix">
                    <input placeholder={this.state.winInput} disabled type="text" />
                    <input id="windows-file-input" className="file-input" type="file" onChange={this.onChangeWin}/>
                    </div>
                    <div className="suffix-btn" type="windows" onClick={this.onUpfileWin}></div>
                    {this.state.percentwin && 
                    (
                        <div>
                            <ProgressBar
                                className='post-image__progressBar'
                                now={this.state.percentwin}
                                active={this.state.percentwin === 100}
                            />
                            <div className='form-item-progressBar'>{this.state.showTxtwin}</div>
                        </div>
                    )}
                </li>
                </ol>
            </div>
        </div>
        );
    }
}